.about__container {
    margin-top: 1%;
    display: grid;
    grid-template-columns: 55% 30%;
    gap: 10%;
}

#about {
    margin-top: 0%;
    padding-top: 60px;
    padding-right: 9rem;
    padding-left: 9rem;
}

.bios {
    margin-bottom: 4%;
    font-size:1.18rem;
}

#bio {
    color: #8187AA;
    font-size: large;
    font-weight: 100;
}

ul {
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 4%;
    padding-left: 1rem;
}

li {
    font-size: 18px;
    margin-left: 10px;
    list-style-type:disc;
    color:#8187AA;
 }

 li::marker, .job {
     color:#00ebc7;
 }

 hr {
    border: 0;
    clear:both;
    display:block;
    width: 96%;               
    background-color: white;
    height: 1px;
    margin-top: 3%;
 }

.about__me-image {
    height: 90%;
    border-radius: 2rem;
    overflow:hidden;
}

/* ========================== MEDIA QUERY ============================= */

@media screen and (max-width:1024px) {
    .about__container {
        display:table;
    }
    h2, p {
        text-align: center;
    }
    p {
        padding-bottom: 1rem;
    }
}

@media screen and (max-width:600px) {
    .about__container {
        display:table;
    }
    h2, p {
        text-align: center;
    }
    p {
        padding-bottom: 2rem;
    }
}